* {
  margin: 0;
}

html {
  --header-height: 60px;
  --link-color: #00acdc;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  width: 100%;
  max-width: 1920px;
}

.logo-nav-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.logo-img {
  margin-left: 50px;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  align-items: center;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
